@charset "UTF-8";

@import "base";
@import "./mian.scss";

.table-top{@include flex('space-between', 'flex-end'); margin-bottom: 20px;}
.table-top p{font-size: 16px;}
.table-top .search{@include flex('space-between'); padding: 10px 14px; width: 240px; background: #f1f1f1; border-radius: 3px;}
.table-top .search input[type=text]{width: 100%; font-size: 16px;}
.table-top .search .ico-search{margin-left: 10px; display: inline-block; @include bg('common/ico-search.png'); width: 16px; height: 16px; vertical-align: middle;}

.no-result{@include flex(); flex-direction: column; padding: 150px 0;}
.no-result::before{content: ''; display: block; margin-bottom: 30px; width: 79px; height: 79px; @include bg('common/ico-no-list.png');}

.paging{margin-top: 80px; @include flex('center');}
.paging > button{width: 18px; height: 18px;}
.paging button.prev-more{@include bg('common/ico-arrow-left2.png');}
.paging button.prev{@include bg('common/ico-arrow-left.png');}
.paging button.next-more{@include bg('common/ico-arrow-right2.png');}
.paging button.next{@include bg('common/ico-arrow-right.png');}
.paging .page-list{margin: 0 20px;}
.paging .page-list button{width: 15px; font-size: 15px;}
.paging .page-list button.on::after{content: ''; display: block; width: 100%; height: 1px; background: #212121;}
.paging .page-list button.on{font-weight: 700;}
.paging .page-list button + button{margin-left: 5px;}

.contents.faq, .contents.notice, .contents.contact{padding-bottom: 150px;}

/* FAQ */
.accordion-list{border-top: 1px solid #000; border-bottom: 1px solid #000;}
.accordion-list .item .tit{@include flex(); padding: 11px 14px 11px 26px; border-bottom: 1px solid #ccc;}
.accordion-list .item .arrow-down{display: block; flex-shrink: 0; margin-left: auto; width: 26px; height: 26px; @include bg('common/ico-arrow-down.png'); transition: all 0.3s;}
.accordion-list .item .arrow-down.on{background-image: url('/assets/img/common/ico-close.png');}
.accordion-list .item .icon{display: inline-flex; align-items: center; justify-content: center; flex-shrink: 0; margin-right: 26px; width: 36px; height: 36px; color: #fff; border-radius: 50%;}
.accordion-list .item .icon.ico-quest{background: #212121;}
.accordion-list .item .icon.ico-answer{background: #ddcbb9; vertical-align: top;}
.accordion-list .item p{width: 86%; font-size: 16px; line-height: 2;}
.accordion-list .item .detail{display: none; padding: 40px 14px 45px 26px; border-bottom: 1px solid #ccc; background: #f6f6f6;}
.accordion-list .item .detail p{display: inline-block;}

/* notice */
.notice .table-wrap .table{ font-family: $suit; border-top: 1px solid #000; border-bottom: 1px solid #000; font-size: 16px;}
.notice .table-wrap table{width: 100%; }
.notice .table-wrap table col:nth-child(1){width: 10%;}
.notice .table-wrap table col:nth-child(2){width: auto;}
.notice .table-wrap table col:nth-child(3){width: 10%;}
.notice .table-wrap table col:nth-child(4){width: 15%;}
.notice .table-wrap table tr td{padding: 20px 0; text-align: center;}
.notice .table-wrap table thead tr{font-weight: 600; border-bottom: 1px solid #ccc; background: #f6f6f6;}
.notice .table-wrap table tbody tr + tr{border-top: 1px solid #ccc;}

.notice .detail-view .table .tit-con{padding: 28px; font-size: 16px; font-weight: 700; background: #f6f6f6; border-bottom: 1px solid #ccc;}
.notice .detail-view .table .tit-con li{display: flex;}
.notice .detail-view .table .tit-con li + li{margin-top: 32px;}
.notice .detail-view .table .tit-con .tit{margin-left: 20px;}
.notice .detail-view .table .tit-con p + p{margin-left: 35px;}
.notice .detail-view .table .tit-con em{font-weight: 400;}
.notice .detail-view .table .txt-con{padding: 60px 28px 80px; font-size: 16px; line-height: 2;}

.notice .detail-view .list-link a{@include flex('space-between'); padding: 12px 28px; border-top: 1px solid #ccc;}
.notice .detail-view .list-link .btn{font-size: 16px;}
.notice .detail-view .list-link .btn .tag{display: inline-block; padding: 12px 18px; font-size: 14px; border-radius: 3px; background: #f1f1f1; flex-shrink: 0;}
.notice .detail-view .list-link .btn .tit{padding: 0 30px; width: 74%;}

/* contact */
.contact .deco-txt{margin-bottom: 170px; font-size: 20px; font-weight: 600; text-align: center;}
.contact .quote-mark::before{content: ''; display: block; margin: 0 auto 30px; width: 30px; height: 24px; @include bg('content/ico-quote-mark.png');}
.contact .info-wrap{display: flex;}
.contact .info-con{margin-right: 155px; width: 35%; flex-shrink: 0;}
.contact .info-con .txt-list{margin-top: 50px; font-size: 16px;}
.contact .info-con .txt-list dt{margin-bottom: 4px; font-weight: 700;}
.contact .info-con .txt-list dd{margin-bottom: 30px; }
.contact .info-con .sns-list{display: flex;}
.contact .info-con .sns-list li + li{margin-left: 20px;}
.contact .info-con .sns-list li a{display: inline-block; width: 65px; height: 65px;}
.contact .info-con .sns-list li.instagram a{@include bg('common/ico-instagram.png');}
.contact .info-con .sns-list li.facebook a{@include bg('common/ico-facebook.png');}
.contact .info-con .sns-list li.youtube a{@include bg('common/ico-youtube.png');}
.contact .input-con .input-list {margin-top: 60px;}
.contact .input-con .input-list .input{@include flex(); flex-wrap: wrap;}
.contact .input-con .input-list .input + .input{margin-top: 20px;}
.contact .input-con .input-list .input.line{flex-wrap: nowrap; border-bottom: 1px solid #ccc;}
.contact .input-con .input-list .input label{@include flex(); width: 100%; font-size: 16px;}
.contact .input-con .input-list .input input[type=text],
.contact .input-con .input-list .input input[type=number],
.contact .input-con .input-list .input input[type=email]{ width: 100%; height: 50px;}
.contact .input-con .input-list .input textarea{padding: 12px; width: 100%; height: 180px; border:1px solid #ccc;}
.contact .input-con .input-list .input .tit{ margin-bottom: 12px; flex-shrink: 0; font-size: 16px; font-weight: 700;}
.contact .input-con .input-list .input.line .tit{ margin-bottom: 0; margin-right: 20px; flex-shrink: 0;  font-size: 16px; font-weight: 700;}
.contact .input-con .input-list .input .bar{margin: 0 20px;}
.contact .input-con .input-list .line-box{overflow: hidden auto; margin-bottom: 24px; padding: 12px; height: 180px; font-size: 14px; font-weight: 300; border:1px solid #ccc;}
.contact .input-con .input-list .line-box dl{margin-top: 30px;}
.contact .input-con .input-list .line-box dt{font-weight: 600;}
.contact .input-con .input-list .line-box dd{margin-top: 8px;}
.contact .input-con .input-list .chk-input input[type="checkbox"]{@include blind();}
.contact .input-con .input-list .chk-input input[type="checkbox"]:checked ~ label .icon-chk{position: relative; background: #ddcbb9; border-color: #ddcbb9;}
.contact .input-con .input-list .chk-input input[type="checkbox"]:checked ~ label .icon-chk::after{content: ''; display: block; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%) rotate(-45deg);     margin-top: -2px; width: 8px; height: 4px; border: 2px solid #fff; border-top: 0; border-right: 0;}
.contact .input-con .input-list .chk-input label .icon-chk{content: ''; display: inline-block; margin-right: 10px;width: 20px; height: 20px; border: 1px solid #acacac; border-radius: 4px;}

/* about - ceo */
.ceo .greetings{padding: 204px 0 100px;}
.ceo .greetings .inner-l{position: relative; @include flex('space-between', 'flex-end');}
.ceo .greetings .sub-tit{padding-bottom: 130px; width: 510px;}
.ceo .greetings .desc li + li{margin-top: 20px;}
.ceo .greetings .img-con{@include flex('', 'flex-end'); }
.ceo .greetings .img-con .caption{margin-right: 20px; font-size: 19px; font-weight: 500;}
.ceo .greetings .img-con img{width: 355px;}

.ceo .history{padding: 384px 0 160px; }
.ceo .history .inner-l{@include flex('', 'flex-end');}
.ceo .history .img-con{margin-right: 107px; width: 520px; flex-shrink: 0;}
.ceo .history .sub-tit .tit-l{margin-bottom: 32px; text-align: left;}
.ceo .history .sub-tit .desc li + li{margin-top: 5px;}

/* business - currency */
.currency .storage{padding: 112px 0;}
.currency .storage .inner-l{ @include flex('space-between', 'flex-end');}
.currency .storage .sub-tit{margin-right: 100px; width: 620px;}
.currency .storage .img-con{width: 520px;}

.currency .factory{padding: 407px 0 95px;}
.currency .factory .inner-l{@include flex('space-between', 'flex-end');}
.currency .factory .sub-tit{margin-right: 100px; width: 530px;}
.currency .factory .img-con{width: 695px;}

/* business - bakery */
.bakery .feature{padding: 160px 0 94px}
.bakery .feature .inner-l{@include flex('space-between', 'flex-end');}
.bakery .feature .sub-tit{width: 520px; margin-right: 30px;}
.bakery .feature .sub-tit ~ .info-con .img-con{margin-left: auto;}
.bakery .feature .info-con{width: 750px; text-align: right;}
.bakery .feature .info-con.left{text-align: left;}
.bakery .feature .info-con .txt-con .desc{margin: 40px 0 120px;}
.bakery .feature .info-con .img-con{width: 70%;}

/* business - academy */
.academy .curriculum{padding: 204px 0 93px;}
.academy .curriculum .info-con{display: flex; margin-top: 320px;}
.academy .curriculum .info-con .img-con{margin-right: 62px; width: 558px;}
.academy .curriculum .info-con .sub-tit{width: 796px;}

.academy .setting{padding: 232px 0 182px;}
.academy .setting .sub-tit{margin: 0 auto 115px; width: 750px; text-align: center;}
.academy .setting .img-list{display: grid; grid-template-columns: repeat(3, 1fr); gap: 40px;}

.academy .feature{padding: 56px 0;}
.academy .feature .inner-l{@include flex('space-between');}
.academy .feature .sub-tit{margin-right: 50px; width: 570px;}
.academy .feature .img-con{width: 960px;}

/* leaven */
.leaven .study{padding: 160px 0 94px;}
.leaven .study .info-con{@include flex('space-between');}
.leaven .study .txt-con{margin-bottom: 236px;}
.leaven .study .sub-tit{width: 915px;}
.leaven .study .sub-tit .desc{width: 62%;}
.leaven .study .img-con{margin-left: 100px; width: 521px;}

.leaven .bread{padding: 260px 0;}
.leaven .bread .inner-l{@include flex();}
.leaven .bread .img-con{margin-right: 150px; width: 830px;}
.leaven .bread .sub-tit{width: 450px;}

.leaven .people{padding: 150px 0 180px;}
.leaven .people .sub-tit .tit-l{margin-bottom: 160px;}
.leaven .people .info-wrap{@include flex('space-between');}
.leaven .people .info-con{@include flex('','flex-start'); margin-right: 60px;}
.leaven .people .info-con .img-con{margin-right: 60px; width: 330px;}
.leaven .people .info-con .img-con a{margin-top: 80px; width: 100%;}
.leaven .people .info-con .txt-con .name{margin-bottom: 100px; font-family: $myeongjo; font-size: 66px; font-weight: 700;}
.leaven .people .info-con .txt-con .desc li + li{margin-top: 5px;}
.leaven .people .quote-txt{@include flex(); width: 530px; height: 674px; font-size: 29px; font-weight: 700; word-break: keep-all; @include bg('/content/img-leaven-03-bg.png');}

.leaven .kind{padding: 368px 0 164px;}
.leaven .kind .inner-l{@include flex('space-between');}
.leaven .kind .img-con{width: 730px;}
.leaven .kind .sub-tit{margin-right: 100px; width: 470px; text-align: right;}

.leaven .mean{padding: 120px 0;}
.leaven .mean .inner-l{@include flex('space-between');}
.leaven .mean .sub-tit{margin-right: 100px; width: 630px;}
.leaven .mean .sub-tit .desc{width: 70%;}
.leaven .mean .img-con{margin-right: 40px; width: 670px;}

/* cafe */
.cafe{}
.cafe .store{padding: 148px 0 75px;}
.cafe .store .inner-l{@include flex('space-between', 'flex-end');}
.cafe .store .txt-wrap{margin-right: 100px; width: 540px;}
.cafe .store .txt-wrap .sub-tit{margin-bottom: 40px;}
.cafe .store .txt-wrap .desc{margin-bottom: 30px;}
.cafe .store .txt-wrap .caption{margin-top: 12px; font-size: 15px; font-weight: 700;}
.cafe .store .img-wrap{width: 830px;}
.cafe .store .img-wrap .map{margin-top: 44px; width: 100%; height: 540px; background-color: #000;}
.cafe .store .info-con + .info-con{margin-top: 40px;}
.cafe .store .info-con > dt{margin-bottom: 20px; font-family: $myeongjo; font-size: 21px; font-weight: 800;}
.cafe .store .info-con > dd{font-size: 16px;}
.cafe .store .info-con > dd strong{display: block; font-weight: 700;}
.cafe .store .info-con > dd + dd{margin-top: 30px;}
.cafe .store .info-con .contact li{display: flex;}
.cafe .store .info-con .contact li + li{margin-top: 5px;}
.cafe .store .info-con .contact li strong{margin-right: 10px; width: 70px; flex-shrink: 0;}
.cafe .store .info-con .contact .way{display: grid; grid-template-columns: 52px auto; gap: 10px 0;}
.cafe .store .info-con .social-btn{@include flex(); color: #fff;}
.cafe .store .info-con .social-btn a{line-height: 1;}
.cafe .store .info-con .social-btn a::before{content: ''; display: inline-block; margin-right: 10px; width: 20px; height: 20px;}
.cafe .store .info-con .social-btn a + a{margin-left: 10px;}
.cafe .store .info-con .social-btn .instagram{background-color: #df5341;}
.cafe .store .info-con .social-btn .instagram::before{@include bg('common/icon-insta.jpg');}
.cafe .store .info-con .social-btn .naver{background-color: #50b747;}
.cafe .store .info-con .social-btn .naver::before{@include bg('common/icon-naver.jpg');}

@media (max-width: 1700px){
  .leaven .kind .sub-tit{margin-left: 60px; margin-right: 0;}
}

