  .leaven .study{padding: 100px 0;}
  .leaven .study .txt-con{margin-bottom: 100px;}
  .leaven .study .info-con{flex-wrap: wrap;}
  .leaven .study .info-con .img-con{margin-left: 0; margin-top: 80px;}
  .leaven .bread .img-con{margin-right: 50px;}
  .leaven .bread{padding: 100px 0;}
  .leaven .people{padding: 100px 0;}
