  .leaven .kind{padding: 100px 0;}
  .leaven .kind .inner-l{flex-wrap: wrap;}
  .leaven .kind .sub-tit{margin-left: auto; margin-top: 80px;}
  .leaven .mean{padding: 100px 0;}
}

@media (max-width: 1080px){
  .ceo .history .img-con{margin-right: 50px;}
  .bakery .feature .inner-l{flex-wrap: wrap;}
  .bakery .feature .sub-tit{margin-right: 0; margin-top: 60px; order: 1;}
  .bakery .feature .info-con{margin-left: auto;}
  .bakery .feature .info-con.left{margin-left: 0; margin-right: auto;}

  .leaven .mean .inner-l{flex-wrap: wrap;}
  .leaven .mean .sub-tit{margin-right: 0; margin-bottom: 80px;}

  .cafe .store{padding: 100px 0;}
  .cafe .store .inner-l{flex-wrap: wrap;}
  .cafe .store .txt-wrap{margin-right: 0; margin-bottom: 80px;}
  .cafe .store .img-wrap{width: 80%;}
}

@media (max-width: 980px) {
  .contact .info-con{margin-right: 60px; width: 40%;}
  .ceo .greetings{padding-top: 100px;}
  .ceo .greetings .inner-l{flex-wrap: wrap;}
  .ceo .greetings .sub-tit{width: 100%;}
  .ceo .greetings .sub-tit .tit-l{margin-bottom: 30px;}
  .ceo .greetings .img-con{margin-left: auto;}

  .ceo .history{padding-top: 100px;}
  .ceo .history .inner-l{flex-wrap: wrap;}
  .ceo .history .img-con{margin-right: 0; margin-bottom: 40px; width: 520px;}
  .ceo .history .sub-tit{width: 100%;}

  .currency .storage .inner-l{flex-wrap: wrap;}
  .currency .storage .sub-tit{margin-right: 0; margin-bottom: 80px;}

  .bakery .feature .sub-tit{width:100%;}
  .bakery .feature .sub-tit .desc br{display: none;}
  .bakery .feature .info-con{width: 100%;}

  .academy .curriculum{padding: 100px 0;}
  .academy .setting .sub-tit{width: 100%;}
  .academy .setting .img-list{grid-template-rows: repeat(3, 1fr); grid-template-columns: auto; margin: 0 auto; width: 70%; }

  .leaven .study .sub-tit{width: 100%;}
  .leaven .bread .inner-l{flex-wrap: wrap;}
  .leaven .bread .img-con{margin-right: 0; margin-bottom: 80px; width: 80%;}
  .leaven .people .info-con .img-con{margin-right: 40px;}
}

